<!--特惠购客户管理-->
<template>
  <div>
    <el-form class="account-plat-formitem" label-position="left" :model="ruleForm" ref="ruleForm" :rules="ruleFormrules"
             label-width="130px"
             style="text-align: left;font-size:20px;font-weight:
      bold;" @submit.native.prevent>
      <el-card class="account-plat-card1 account-plat-card2" shadow="always" :body-style="cardBodyStyle" style="">
        <div slot="header" style="font-size: 14px;font-weight: bold; " @click="confCardBodyHeigth">
          <el-row @click="confCardBodyHeigth">
            <el-col :md="22">
              <span style="margin-top: 5px;display: block">客户列表</span>
            </el-col>
            <!--   查询框隐藏显示按钮         -->
            <el-col :md="1" @click="confCardBodyHeigth">
              <el-link :icon="icon1" :underline="false"
                       style="float: right;text-decoration: none;font-size:20px;height: 10px;line-height: 10px;margin-top: 10px"></el-link>
            </el-col>
          </el-row>
        </div>
        <div v-show="cardBodyHeight" style="margin: 10px 0">
            <div style="width: 100%;display: flex;justify-content: space-between">
              <div style="width: 50%">
                <el-form-item label="手机号码:" prop="mau_guest_phone" label-width="130px">
                <el-input :maxlength="11" placeholder="请输入手机号" v-model="ruleForm.mau_guest_phone"
                          style="width: 300px"></el-input>
                </el-form-item>
              </div>
              <div style="width: 170px;margin: 0 50px 0">
                  <el-button type="primary" @click="query_name">查询</el-button>
                  <el-button style="float:right;" type="primary" @click="platformAddRole('ruleForm')"><i
                    class="el-icon-edit">新增</i></el-button>
              </div>
            </div>
        </div>
      </el-card>
    </el-form>
    <el-card>
      <!--  页面表格 start   -->
      <el-table
        class="account-plat-fixed-right account-plat-fixed"
        border
        ref="tableData"
        :data="tableData"
        tooltip-effect="dark"
        @sort-change="sortColumn"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
        <!--        <el-table-column
                  type="selection"
                  :selectable="isSelect"
                  width="55">
                </el-table-column>-->
        <el-table-column
          fixed
          align="center"
          type="index"
          label="序号"
          width="50">
        </el-table-column>
        <el-table-column
          prop="mau_guest_phone_new"
          label="手机号"
          align="center"
          show-overflow-tooltip
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="zhengchang"
          label="赋予特惠价购买次数"
          align="center"
          show-overflow-tooltip
          width="100"
        >
          <template slot-scope="scope">
            <div v-if="scope.row.mau_del_status_new === '1'">{{scope.row.zhengchang}}</div>
            <div v-if="scope.row.mau_del_status_new === '2'">0</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="yiyong"
          label="已用次数"
          align="center"
          show-overflow-tooltip
          width="100"
        >
          <template slot-scope="scope">
            <div v-if="scope.row.mau_del_status_new === '1'">{{scope.row.yiyong}}</div>
            <div v-if="scope.row.mau_del_status_new === '2'">0</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="zuizhong"
          label="未用次数"
          align="center"
          show-overflow-tooltip
          width="100"
        >
          <template slot-scope="scope">
            <div v-if="scope.row.mau_del_status_new === '1'">{{scope.row.zuizhong}}</div>
            <div v-if="scope.row.mau_del_status_new === '2'">0</div>
          </template>
        </el-table-column>
<!--        <el-table-column-->
<!--          prop="mau_writetime_new"-->
<!--          label="最后一次操作时间"-->
<!--          align="center"-->
<!--          show-overflow-tooltip-->
<!--          width="200"-->
<!--        >-->
<!--        </el-table-column>-->
<!--        <el-table-column-->
<!--          prop="cui_realname"-->
<!--          label="操作员"-->
<!--          align="center"-->
<!--          show-overflow-tooltip-->
<!--          width="100"-->
<!--        >-->
<!--        </el-table-column>-->
        <el-table-column
          prop="mau_del_status_new"
          label="状态"
          align="center"
          show-overflow-tooltip
          width="120"
        >
          <template slot-scope="scope">
            <el-tag v-if="scope.row.mau_del_status_new === '1'" size="small" effect="dark" type="success">正常</el-tag>
            <el-tag v-if="scope.row.mau_del_status_new === '2'" size="small" effect="dark" type="info">删除</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          min-width="200"
          align="center"
        >
          <template slot-scope="scope">
            
          <el-button v-if="scope.row.mau_del_status_new === '1'" @click.native.stop="add_raffle(scope.$index,scope.row)" type="text" size="small">次数管理</el-button>
          <el-button v-if="scope.row.mau_del_status_new === '1' & scope.row.yiyong == '0'" @click.native.stop="delete_phone(scope.$index,scope.row)" type="text" size="small">删除</el-button>
            <!-- <el-tooltip content="次数管理" placement="top" v-if="scope.row.mau_del_status_new === '1'">
              <el-button
                type="success" icon="el-icon-document-add" circle
                size="mini"
                @click.native.stop="add_raffle(scope.$index,scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip content="删除" placement="top" v-if="scope.row.mau_del_status_new === '1' & scope.row.yiyong == '0'">
              <el-button
                type="danger" icon="el-icon-switch-button" circle
                size="mini"
                @click.native.stop="delete_phone(scope.$index,scope.row)"></el-button>
            </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>
      <!--  页面表格 end   -->
      <!--page start-->
      <el-pagination
        style="margin-top: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParam.currentPage"
        :page-sizes="[10, 20, 50, 100,200]"
        :page-size="queryParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryParam.totalRow">
      </el-pagination>
      <!--page end-->
    </el-card>
    <!--页面 end-->

    <!--    <el-dialog-->
    <!--      :show-close="false"-->
    <!--      :close-on-press-escape="false"-->
    <!--      :close-on-click-modal="false"-->
    <!--      :visible.sync="dialogVisible"-->
    <!--      width="30%"-->
    <!--      class="account-plat-dialog"-->
    <!--    >-->
    <!--      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;margin-bottom: 20px">-->
    <!--        <span>{{ this.tableDialgTitle }}</span>-->
    <!--      </div>-->
    <!--      <el-form :model="phoneForm" ref="phoneForm" label-width="160px" style="position: relative"-->
    <!--               :rules="phoneFormrules">-->
    <!--        <el-row :gutter="24">-->
    <!--          <el-col :md="12">-->
    <!--            <el-form-item label="手机号码:" prop="mau_guest_phone" label-width="130px">-->
    <!--              <el-input placeholder="请输入手机号" v-model="phoneForm.mau_guest_phone"-->
    <!--                        style="width: 300px"></el-input>-->
    <!--            </el-form-item>-->
    <!--          </el-col>-->
    <!--        </el-row>-->
    <!--      </el-form>-->
    <!--      <div slot="footer" class="dialog-footer">-->
    <!--        <el-button @click="cancelForm('phoneForm')">取 消</el-button>-->
    <!--        <el-button type="primary" @click="submitForm('phoneForm')">确 定</el-button>-->
    <!--      </div>-->
    <!--    </el-dialog>-->

    <!--  弹出窗口  -->
    <el-dialog :visible.sync="commodityFormVisible"
               :close-on-press-escape="false"
               :close-on-click-modal="false"
               :show-close="false"
               class="account-plat-dialog"
               width="60%"

    >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ this.tableDialgTitle }}</span>
      </div>
      <!--form start-->
      <el-form class="account-plat-formitem" :model="commodityForm" :rules="commodityFormrule"
               ref="commodityForm" label-width="120px"
               style="text-align: left;font-size:20px;font-weight: bold;margin-top: 10px;">
        <el-row :gutter="24">
          <el-col :md="22" style="margin: 20px 0">
            <el-button style="float:right;" type="primary" @click="prize_add"><i
              class="el-icon-edit">添加特惠价购买次数</i></el-button>
          </el-col>
          <el-table
            :data="tableData_prize"
            @sort-change="sortColumn"
            style="margin-left: 50px;width: 90%;margin-bottom: 50px"
            class="account-plat-fixed-right account-plat-fixed "
            border
            ref="tableData_prize"
            tooltip-effect="dark"
            :row-style="{'height':'10px'}"
            :cell-style="{'padding':'5px 0px'}"
            :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
          >
            <el-table-column
              fixed
              align="center"
              type="index"
              label="序号"
              width="50">
            </el-table-column>
            <el-table-column
              prop="mau_guest_phone"
              label="手机号"
              align="center"
              show-overflow-tooltip
              width="150"
            >
            </el-table-column>
            <el-table-column
              label="商品名称"
              align="center"
              show-overflow-tooltip
              width="250"
            >
              <template slot-scope="scope">
                <div v-if="scope.row.mau_get_status =='1'">{{ scope.row.mbgi_goods_name }}</div>
                <div v-if="scope.row.mau_get_status ==='2' || scope.row.mau_get_status ==='3' ">未购买</div>
              </template>
            </el-table-column>
            <el-table-column
              label="金额（元）"
              align="center"
              show-overflow-tooltip
              width="200"
            >
              <template slot-scope="scope">
                <div v-if="scope.row.mau_get_status =='1'">{{ scope.row.mbs_money_1 }}</div>
                <div v-if="scope.row.mau_get_status ==='2' || scope.row.mau_get_status ==='3' ">未购买</div>
              </template>
            </el-table-column>
            <el-table-column
              label="购买时间"
              align="center"
              show-overflow-tooltip
              width="150"
            >
              <template slot-scope="scope">
                <div v-if="scope.row.mau_get_status =='1'">{{ scope.row.mbs_writetime }}</div>
                <div v-if="scope.row.mau_get_status ==='2' || scope.row.mau_get_status ==='3' ">未购买</div>
              </template>
            </el-table-column>
            <el-table-column
              fixed="right"
              label="操作"
              min-width="150"
              align="center"
            >
              <template slot-scope="scope">
                <el-tooltip content="删除" placement="top" v-if="scope.row.mau_get_status ==='2' || scope.row.mau_get_status ==='3' ">
                  <el-button
                    type="danger" icon="el-icon-switch-button" circle
                    size="mini"
                    @click.native.stop="User_prize_delete(scope.$index,scope.row)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose()">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "PreferentialCustomers",
  data() {
    let mau_guest_phone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('手机号不能为空'))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('请输入正确的手机号!'))
      } else {
        callback()
      }
    };
    return {
      ruleForm: {
        mau_guest_phone: '',
      },
      ruleFormrules:{
        mau_guest_phone: [
          {required: true, validator: mau_guest_phone, trigger: 'blur'}
        ],
      },
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      icon1: 'el-icon-caret-top',
      cardBodyHeight: true,
      tableData: [],
      /** 查询参数 */
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      // dialogVisible: false,
      tableDialgTitle: '',
      // phoneForm: {
      //   mau_guest_phone: ''
      // },
      // phoneFormrules: {
      //   mau_guest_phone: [
      //     {required: true, validator: mau_guest_phone, trigger: 'blur'}
      //   ],
      // }
      commodityFormVisible:false,
      commodityForm:{

      },
      commodityFormrule:{

      },
      tableData_prize:[],
      phone:'',//拿到的手机号
      mau_uuid:''//购买机会唯一ID
    }
  },
  created() {
    this.queryParam.currentPage = 1;
    this.query_name();
  },
  methods: {
    /** 电话号校验 */
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false
      } else {
        return true
      }
    },
    /**----分页方法-------start----*/
    handleSizeChange(val) {
      //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.query_name();
    },
    handleCurrentChange(val) {
      //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.query_name();
    },
    /** 设置查询框隐藏显示  */
    confCardBodyHeigth() {
      if (this.cardBodyHeight === false) {
        this.cardBodyHeight = true;
        this.cardBodyStyle = 'padding-top:10px;padding-bottom:10px;';
        this.icon1 = 'el-icon-caret-top';
      } else {
        this.cardBodyHeight = false;
        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon1 = 'el-icon-caret-bottom';
      }
      //  console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
    //查询
    query_name() {
      let that = this;
      that.tableData = [];
      let _jsondata_ = {
        perPageSize: that.queryParam.pageSize,
        currentPage: that.queryParam.currentPage,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        mau_guest_phone: that.ruleForm.mau_guest_phone,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Customer_list_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;
        } else {
          console.info("手机号查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '手机号查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '手机号查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //新增
    // platformAddRole(){
    //   this.dialogVisible = true;
    //   this.tableDialgTitle = '新增手机号';
    // },
    /**  表格列排序设置   */
    sortColumn(column) {
      //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        //  console.info("属性名称："+ column.prop+",排序方式："+column.order);

        this.sortOrderCol = column.prop + " " + orderFlag;
        //  console.info("用户选择排序列："+this.sortOrderCol);
        this.query_name();
      } else {
        this.sortOrderCol = '';
      }
    },
    //添加特惠价购买次数
    add_raffle(index,row) {
      this.tableDialgTitle = '次数管理';
      this.commodityFormVisible = true;
      this.phone = row.mau_guest_phone_new;
      this.mau_uuid = row.mau_uuid;
      this.phone_lucky_draw();
    },
    //删除
    delete_phone(index,row) {
      this.phone = row.mau_guest_phone_new;
      this.$confirm('确定要删除么?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let that = this;
        let _jsondata_ = {
          mau_guest_phone: that.phone,
          mau_cui_id_2: that.customGlobal.userInfoExt.cui_uuid,
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Customer_list_update,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        a1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">删除成功</span>'
            });
            this.queryParam.currentPage = 1;
            this.query_name();
          } else {
            console.info("删除失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消保存'
        });
      });
    },
    //取消
    // cancelForm(){
    //   this.dialogVisible = false;
    // },
    //新增
    platformAddRole(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.phone = this.ruleForm.mau_guest_phone;
          this.add_phone();
        } else {
          return false;
        }
      });
    },
    //添加奖品
    prize_add(){
      this.add_phone();
      this.phone_lucky_draw();
    },
    //取消
    handleClose() {
      // this.ruleForm.lau_guest_phone = ''
      this.commodityFormVisible = false;
      this.queryParam.currentPage = 1;
      this.query_name();
    },
    //查询一个手机号有几次抽奖机会
    phone_lucky_draw() {
      let that = this;
      that.tableData_prize = [];
      let _jsondata_ = {
        mau_guest_phone: that.phone,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: 100,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Number_of_purchases_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          that.tableData_prize = res.data.rows;
          // this.queryParam.currentPage = 1;
          // this.query_name();
        } else {
          console.info("购买次数查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '购买次数查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '购买次数查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //新增手机号购买次数接口
    add_phone(){
      let that = this;
      let _jsondata_ = {
        mau_guest_phone: that.phone,
        mau_cui_id: that.customGlobal.userInfoExt.cui_uuid,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Add_phone_number_and_purchase_opportunity,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">新增成功 </span>'
          });
          that.ruleForm.mau_guest_phone = '';
          this.queryParam.currentPage = 1;
          this.query_name();
        } else {
          console.info("新增失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '新增出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '新增出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //删除次数
    User_prize_delete(index,row){
      this.$confirm('确定要删除么?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let that = this;
        let _jsondata_ = {
          mau_uuid: row.mau_uuid,
          mau_cui_id_2: that.customGlobal.userInfoExt.cui_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Number_of_purchases_update,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        a1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">删除成功</span>'
            });
            this.phone_lucky_draw();
          } else {
            console.info("删除失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消保存'
        });
      });
    }
  }
}
</script>

<style scoped>
.account-plat-formitem /deep/ .el-form-item {
  margin-bottom: 3px !important;
}

.account-plat-card1 /deep/ .el-card__header {
  /*background:#f5f5f5 !important;*/
  background: #f2f3f8 !important;
  /*height: 10px!important;*/
  padding: 10px !important;
}

.account-plat-card2 /deep/ .el-card__body {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}

.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}
</style>



